<template>
  <v-container class="pa-5">
    <div><span class="text-h5 font-weight-bold">마일리지</span></div>
    <div class="body-2 pt-2">마일리지 유효기간 : 적립 후 6개월</div>
    <div>
      <v-tabs active-class="active-tab" slider-color="black" v-model="tab">
        <v-tab>적립 내역</v-tab>
        <v-tab>사용 내역</v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item>
          <v-row class="pt-3">
            <v-col class="pa-3">
              <v-chip-group mandatory active-class="white--text">
                <v-chip color="#c2935f" class="font-weight-bold" @click="saveM(1)">1개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="saveM(3)">3개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="saveM(6)">6개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="saveM(12)">12개월</v-chip>
              </v-chip-group>
            </v-col>
          </v-row>
          <div>
            <div v-for="save in saveMileage" :key="save.mlno">
              <v-row no-gutters class="mb-3 mt-2">
                <v-col cols="3" class="caption mr-1">{{ new Date(save.mlregdate).toLocaleString() }}</v-col>
                <v-col>{{ save.mlcontent }} </v-col>
                <v-col cols="3" class="font-weight-bold">+ {{ save.mlpoint }}P</v-col>
              </v-row>
              <v-divider />
            </div>
          </div>
        </v-tab-item>

        <v-tab-item>
          <v-row class="pt-3">
            <v-col class="pa-3">
              <v-chip-group mandatory active-class="white--text" column>
                <v-chip color="#c2935f" class="font-weight-bold" @click="usedM(1)">1개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="usedM(3)">3개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="usedM(6)">6개월</v-chip>
                <v-chip color="#c2935f" class="font-weight-bold" @click="usedM(12)">12개월</v-chip>
              </v-chip-group>
            </v-col>
          </v-row>
          <div>
            <div v-for="used in usedMileage" :key="used.mlno">
              <v-row no-gutters class="mb-3 mt-2">
                <v-col cols="3" class="caption">{{ new Date(used.mlregdate).toLocaleString() }}</v-col>
                <v-col>{{ used.mlcontent }} </v-col>
                <v-col cols="3" class="font-weight-bold red--text">- {{ used.mlpoint }}P</v-col>
              </v-row>
              <v-divider />
            </div>
          </div>
        </v-tab-item>
      </v-tabs-items>
    </div>
  </v-container>
</template>

<script>
import MemberApi from "@/apis/member";
export default {
  name: "Mileage",
  data: () => ({
    tab: null,
    saveMileage: null,
    usedMileage: null,
  }),
  methods: {
    async getMileageList(month) {
      try {
        const promise = await MemberApi.mileageList(this.$store.getters.getMno, month);
        console.log(promise);
        this.saveMileage = promise.data.save;
        this.usedMileage = promise.data.used;
      } catch (error) {
        console.log(error);
      }
    },

    saveM(val) {
      this.getMileageList(val);
    },
    usedM(val) {
      this.getMileageList(val);
    },
  },
  created() {
    this.getMileageList(1);
  },
};
</script>

<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
</style>
